// File: _chat.scss

@import '../../styles/_variables';

svg {
    cursor: pointer; }

.chat {
    background-color: $bg-color;//#161f27
    // margin: 5px 0px 5px 0px
    max-height: 800px;
    display: flex;
    justify-content: space-between;

    a {
        color: #cfd9e4;
        text-decoration: none; }

    h1 {
        // height: 5%
        // padding-left: 10px
        font-size: 22px;
        color: white;
 }        // padding-bottom: 10px

    h2 {
        padding-top: 4px;
        padding-left: 10px;
        font-size: 15px;
        border-bottom: 1px solid #ccc; }

    .categories,
    .messages,
    .status-bar {
        height: 100vh;
        background-color: $bg-color; }

    .categories {
        width: 18vw;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .top {
            overflow: hidden;
            overflow-y: scroll;
            width: 100%;

            .topbar {
                width: 100%;
                height: 43px;
                margin-left: 5px;
                display: flex;
                justify-content: space-between;
                border-bottom: 2px solid darken($fg-color, 50);

                .user {
                    font-size: 1.2em;
                    margin: 6px; }

                .plus {
                    height: 30px;
                    margin-right: 6px;
                    font-size: 2em;

                    &:hover,
                    &:active {
                        cursor: pointer;
                        color: darken($fg-color, 30);
                        transition-duration: .2s; } } }

            nav ul {
                list-style: none;
                padding-left: 6px;

                li {
                    // font-size: 1em
                    font: 20px Lato, Arial, Sans-Serif;
                    margin-top: .4em;
                    padding-left: .4em;

                    display: flex;
                    justify-content: space-between;

                    svg {
                        opacity: 0;
                        width: 25px;
                        height: 25px; } }

                li:hover,
                li:active {
                    background-color: #8f9397;
                    border-radius: 5px;

                    a {
                        color: $fg-color; }

                    svg {
                        opacity: 1; } } } } }

    .messages {
        width: 62vw;
        background-color: #2e4153;
        // padding: 1px

        #top {
            display: flex;
            justify-content: space-between;
            height: 43px; //6%
            margin: 0 auto;
            width: 98%;

            h1 {
                padding-top: 3px; }

            #category {
 }                // pass

            #log_out {
                &:hover,
                &:active {
                    cursor: pointer;
                    color: darken(white, 30); } } }

        #chat-screen {
            margin: 0 auto;
            height: calc(100% - 120px); // 82%
            width: 98%;
            background-color: #283848;
            overflow: hidden;
            overflow-y: scroll;

            div {
                display: flex;
                font-size: 14px;
                justify-content: space-between;
                height: fit-content;
                padding-bottom: 1px;

                .author {
                    width: 8%;
                    margin-left: 4px;
                    vertical-align: top; }

                .message {
                    width: 78%;
                    // margin-left: 4px
                    vertical-align: top;
                    display: inline-block; }

                .date {
                    width: 10%;
                    vertical-align: top;
                    text-align: left; } } }

        #input {
            margin-top: 1%;

            textarea {
                position: absolute;
                // float: left
                resize: none !important;
                overflow: hidden !important;
                height: 65px; // 10vh
                width: 60.6vw; // 99%
                margin-left: .7vw;
                background-color: #46637f;
                z-index: 1;
                color: #cfd9e4;

                &::placeholder {
                    color: #cfd9e4; } }

            svg, input {
                float: right;
                z-index: 2;
                position: relative; }

            input {
                // width: 240px
                top: 42px;
                width: 130px;
                left: -10px; }

            svg { // no longer needed
                margin-right: .8vw;
                height: 24px;
                width: 24px;

                path:active,
                path:hover {
                    fill: #fff; } } } }

    .status-bar {
        width: 19vw;
        overflow: hidden;
        overflow-y: scroll;

        .online {
            font-size: .8em;
 }            // color: #4810f6

        .offline {
            font-size: .8em;
            color: #707070; } } }


::-webkit-scrollbar {
    width: 10px;
    background: none;
    box-shadow: none; }

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #a6a6a6; }

@media screen and (max-width: 800px) {
    body {
        .categories {
            display: none !important; }

        .status-bar {
            display: none !important; }

        .messages {
            width: 100vw !important;

            #chat-screen {
                width: 100% !important; } } } }
